// create calculator layout
const createCalcLayout = () => {
  const calcLayout = document.createElement("div");
  calcLayout.classList.add("calc-layout");
  const calcDisplay = document.createElement("div");
  calcDisplay.classList.add("calc-display");
  calcDisplay.textContent = "0";
  calcLayout.appendChild(calcDisplay);
  const calcKeys = document.createElement("div");
  calcKeys.classList.add("calc-keys");
  calcLayout.appendChild(calcKeys);
  const keys = [
    "clear",
    "delete",
    "percent",
    "/",
    7,
    8,
    9,
    "*",
    4,
    5,
    6,
    "-",
    1,
    2,
    3,
    "+",
    0,
    ".",
    "=",
  ];
  keys.forEach((key) => {
    const button = document.createElement("button");
    button.classList.add("calc-key");
    button.textContent = key;
    calcKeys.appendChild(button);
  });
  return calcLayout;
};

export { createCalcLayout };
